.section {
  padding-top: 60px;
  padding-bottom: 60px;
  &__title {
    text-align: center;
    margin-bottom: 20px;
    @include md {
      margin-bottom: 10px; } }
  &__subttl {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 880px;
    margin-bottom: 60px;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 10px; } } }
